<template>
  <div class="blog-list-container" ref="mainContainer" v-loading="isLoading">
    <ul>
      <li v-for="item in data.rows" :key="item.id">
        <div class="thumb" v-if="item.thumb">
          <RouterLink :to="{
            name: 'Detail',
            params: {
              id: item.id
            }

          }">
            <img
              v-lazy="item.thumb"
              :alt="item.title"
              :title="item.title"
            />
          </RouterLink>
        </div>
        <div class="main">
          <RouterLink :to="{
            name: 'Detail',
            params: {
              id: item.id
            }

          }">
            <h2>{{item.title}}</h2>
          </RouterLink>
          <div class="aside">
            <span>日期：{{formatDate(item.createDate)}}</span>
            <span>浏览：{{ item.scanNumber }}</span>
            <span>评论: {{ item.commentNumber }}</span>
            <RouterLink :to="{
              name: 'CategoryBlog',
                params: {
                  categoryId: item.category.id
                }
              }" 
              class="">{{ item.category.name}}</RouterLink>
          </div>
          <div class="desc">
            {{ item.description }}
          </div>

        </div>
      </li>
    </ul>
    <!-- 分页放到这里 -->
    <Empty v-if="data.rows.length === 0 && !isLoading"/>
    <Pager 
    v-if="!isLoading"
    :current="routerInfo.page"
    :limit="routerInfo.limit"
    :total="data.total"
    :visibleNumber="10"
    @pageChange="handlePageChange"
     />
  </div>
</template>

<script>
import fetchData from "@/mixins/fetchData.js";
import mainScroll from "@/mixins/mainScroll.js";
import { getBlogs } from "@/api/blog.js";
import {formatDate} from "@/utils";
import Pager from "@/components/Pager";
import Empty from "@/components/Empty";
export default {
  components: {
      Pager,
      Empty
  },
  mixins: [fetchData({total: 0, rows: []}),mainScroll("mainContainer")],
  computed: {
      routerInfo() {
          const categoryId = +this.$route.params.categoryId || -1;
          const page = +this.$route.query.page || 1;
          const limit = +this.$route.query.limit || 10;
          return {
              categoryId,
              page,
              limit
          }
      }
  },
  watch: {
      async $route() {
          this.isLoading = true;
          this.$refs.container.scrollTop = 0;
          this.data = await this.fetchData();
          this.isLoading = false;

      }


    // $route: {
    //     handler(){
    //         console.log('路由变了呀')
    //     },
    //     deep: false,        //是否监听数据内部属性变化
    //     immediate: false        //是否立即执行一次
    // }
  },
  methods: {
    formatDate,
    async fetchData() {
      return await getBlogs();
    },
    handlePageChange(newPage) {
        const query = {
            page: newPage,
            limit: this.routerInfo.limit
        }
        if( this.routerInfo.categoryId === -1 ) {
            this.$router.push({
            name: 'Blog',
            query,
        })
        } else {
            this.$router.push({
                name: 'CategoryBlog',
                query,
                params: {
                    categoryId: this.routerInfo.categoryId
                }
            })
        }
    }
  },
};
</script>

<style scoped lang="less">
@import "~@/styles/global.less";
@import "~@/styles/var.less";
.blog-list-container {
  line-height: 1.7;
  position: relative;
  padding: 20px;
  overflow-y: scroll;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth;
  ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }
}
li {
  display: flex;
  padding: 15px 0;
  border-bottom: 1px solid @gray;
  .thumb {
    flex: 0 0 auto;
    margin-right: 15px;
    img {
      display: block;
      max-width: 200px;
      border-radius: 5px;
    }
  }
  .main {
    flex: 1 1 auto;
    h2 {
      margin: 0;
    }
  }
  .aside {
    font-size: 12px;
    color: @gray;
    span {
      margin-right: 15px;
    }
  }
  .desc {
    margin: 15px 0;
    font-size: 14px;
  }
}
</style>
