<template>
  <div class="carousel-item-container" ref="container" @mousemove="handleMouseMove" @mouseleave="handleMouseLeave">
    <div class="carousel-img" ref="image" :style="imagePosition">
      <ImageLoader
        @load="this.showWords"
        :src="carousel.bigImg"
        :placeholder="carousel.midImg"
      />
    </div>
    <div class="title" ref="title">
      {{ carousel.title }}
    </div>
    <div class="desc" ref="desc">
      {{ carousel.description }}
    </div>

  </div>
</template>

<script>
import ImageLoader from "@/components/ImageLoader";
export default {
  props: ["carousel"],
  components: {
    ImageLoader,
  },
  data() {
    return {
      titleWidth: 0,
      descWidth: 0,
      containerSize: null,
      imageSize: null,
      mouseX: 0,
      mouseY: 0
    };
  },
  mounted() {
    this.titleWidth = this.$refs.title.clientWidth; //获取文字标题容器宽度
    this.descWidth = this.$refs.desc.clientWidth; //获取文字描述容器宽度
    this.setSize();
    window.addEventListener("resize", this.setSize);
  },
  destroyed() {
      window.removeEventListener('resize',this.resize);
  },
  computed: {
//得到图片坐标
    imagePosition() {
      if (!this.imageSize || !this.containerSize) {
        return;
      }
      const extraWidth = this.imageSize.width - this.containerSize.width; // 多出的宽度
      const extraHeight = this.imageSize.height - this.containerSize.height; //多出的高度
      const left = (-extraWidth / this.containerSize.width) * this.mouseX;
      const top = (-extraHeight / this.containerSize.height) * this.mouseY;
      return {
        transform: `translate(${left}px, ${top}px)`,
      };
    },

    center() {
      return {
        x: this.containerSize.width / 2,
        y: this.containerSize.height / 2
      }
    }
  },
  methods: {
    showWords() {
      //title
      this.$refs.title.style.opacity = 1;
      this.$refs.title.style.width = 0;
      //强制渲染
      this.$refs.title.clientWidth; //reflow
      this.$refs.title.style.transition = "1s";
      this.$refs.title.style.width = this.titleWidth + "px";

      //desc
      this.$refs.desc.style.opacity = 1;
      this.$refs.desc.style.width = 0;
      //强制渲染
      this.$refs.desc.clientWidth; //reflow
      this.$refs.desc.style.transition = "2s 1s";
      this.$refs.desc.style.width = this.descWidth + "px";
    },
    setSize() {
        this.containerSize = {
            width: this.$refs.container.clientWidth,
            height: this.$refs.container.clientHeight
        }

        this.imageSize = {
            width: this.$refs.image.clientWidth,
            height: this.$refs.image.clientHeight
        }
    },
    handleMouseMove(e) {
      // console.log(e);  //鼠标事件对象
      const rect = this.$refs.container.getBoundingClientRect(); //获取dom距离边距的位置
      this.mouseX = e.clientX - rect.left;  //鼠标的位置减去 dom 离窗口的边距  等于 鼠标距离 dom 边框的位置
      this.mouseY = e.clientY - rect.top;
    },
    handleMouseLeave() {
      this.mouseX = this.center.x;
      this.mouseY = this.center.y;
    }
  },
};
</script>

<style lang="less" scoped>
.carousel-item-container {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}

.carousel-img {
  width: 110%;
  height: 110%;
  z-index: -200;
  position: absolute;
  top: 0;
  left: 0;
  transition: 0.3s;
}

.title,
.desc {
  position: absolute;
  left: 30px;
  text-shadow: 1px 0 0 rgba(0, 0, 0, 0.5), -1px 0 0 rgba(0, 0, 0, 0.5),
    0 1px 0 rgba(0, 0, 0, 0.5), 0 -1px 0 rgba(0, 0, 0, 0.5);
  color: #fff;
  letter-spacing: 3px;
  white-space: nowrap;
  overflow: hidden;
  opacity: 0;
}
.title {
  font-size: 1.6em;
  top: calc(50% - 40px);
}
.desc {
  font-size: 1.2em;
  top: 50%;
}
</style>