<template>
    <ul>
        <li v-for="(item, i) in list" :key="i">
            <span @click="handleClick(item)" :class="{active: item.isSelect}">{{item.name}}</span>
            <span v-if="item.aside" class="aside" :class="{active: item.isSelect}" @click="handleClick(item)">{{item.aside}}</span>
            <RightList :list="item.children" @select="handleClick" />

        </li>
    </ul>
</template>

<script>
export default {
    name: 'RightList',
    props: {
        list: {
            type: Array,
            default: () => []
        }
    },
    mounted() {
        
    },
    methods: {
        handleClick(item) {
            if(!item.isSelect) {
                this.$emit('select', item);
            }
        }
    }
}
</script>

<style scoped lang="less">
@import "~@/styles/var.less";
@import "~@/styles/global.less";
    li {
        margin-left: 10px;
        font-size: 14px;
        min-height: 40px;
        line-height: 40px;
        cursor: pointer;
    }
    .active {
        color: @warn;
        font-size: 14px;
        font-weight: bold;
    }
    .aside {
        margin-left: 1em;
        font-size: 12px;
    }
</style>