<template>
  <div class="image-load-container">
    <img class="placeholder" v-if="!everythingDown" :src="placeholder" />
    <img
      @load="handleLoad"
      :src="src"
      :style="{ opacity: originOpacity, transition: `${duration}ms` }"
    />
  </div>
</template>

<script>
export default {
  data() {
    return {
      originLoad: false,
      everythingDown: false
    };
  },
  props: {
    src: {
      type: String,
      required: true,
    },
    placeholder: {
      type: String,
      required: true,
    },
    duration: {
      type: Number,
      default: 500,
    },
  },
  methods: {
    handleLoad() {
      setTimeout(() => {
        this.originLoad = true;
        this.$emit("load");
        this.everythingDown = true;
      }, this.duration);
    },
  },
  computed: {
    originOpacity() {
      return this.originLoad ? 1 : 0;
    },
  },
};
</script>

<style scopted lang="less">
@import "~@/styles/mixture.less";
.image-load-container {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  img {
    .self-fill();
    object-fit: cover;
  }

  .placeholder {
    filter: blur(2vw);
  }
}
</style>