<template>
  <div class="home-container" ref="container" @mousewheel="handleWheel" v-loading="loading">
    <ul class="carousel-container" :style="{ marginTop }" @transitionend="handleTransitionEnd">
      <li v-for="item in data" :key="item.id">
        <CarouselItem :carousel="item" />
      </li>
    </ul>

    <div v-show="index >=1 " class="icon arrow-up" @click="switchTo(index - 1)">
      <Icon type="arrowUp" />
    </div>
    <div v-show="index < this.data.length -1 "  class="icon arrow-down" @click="switchTo(index + 1)">
      <Icon type="arrowDown" />
    </div>

      <ul class="interceptor">
        <li :class="{active: index === i }" v-for="(item, i) in data" :key="item.id" @click="switchTo(i)"></li>
      </ul>
  </div>
</template>

<script>
import CarouselItem from "./CarouselItem.vue";
import Icon from "@/components/Icon";
import { mapState } from 'vuex';
export default {
  components: {
    CarouselItem,
    Icon,
  },
  data() {
    return {
      // banners: [],  // 数据数组
      index: 0,   //轮播图当前第几页
      containerHeight: 0,    //整个容器的高度
      switching: false,    //是否在切换中
      isLoading: true
    };
  },
  computed: {
    marginTop() {
      return -this.index * this.containerHeight + 'px';
    },
    ...mapState('banner',['loading','data'])
  },
  // async created() {
  //   this.banners = await getBanners();
  //   this.show = false;
  //   this.isLoading = false;
  // },
  created() {
    this.$store.dispatch("banner/fetchBanner")
  },
  mounted() {
    this.containerHeight = this.$refs.container.clientHeight;
    window.addEventListener('reSize', this.handleResize);
  },
  destroyed() {
    window.removeEventListener('resize', this.handleResize);
  },
  methods: {
    switchTo(i) {
      this.index = i;
    },
    handleWheel(e) {
      if( this.switching ) {
        return;
      }
      if( e.deltaY > 5 && this.index < this.data.length -1 ) {
        //向下滑动
        this.index++;
        this.switching = true;
      } else if( e.deltaY < -5 && this.index > 1 ) {
        //向上滑动
        this.index --;
        this.switching = true;
      }
    },
    handleTransitionEnd() {
      this.switching = false;
    },
    handleResize() {
      this.containerHeight = this.$refs.container.clientHeight;
    }
  }
};
</script>

<style scoped lang="less">
@import "~@/styles/global.less";
@import "~@/styles/mixture.less";
@import "~@/styles/var.less";
  .home-container {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
    // caret-color: transparent;
    ul {
      margin: 0;
    }
  }
  .carousel-container {
    width: 100%;
    height: 100%;
    transition: 500ms;
    li {
      width: 100%;
      height: 100%;
    }
  }
  .icon {
    position: absolute;
    left: 50%;
    font-size: 2em;
    top: 50%;
    caret-color: transparent;
    transform: translate(-50%);
    color: @gray;
    @gap: 25px;
    cursor: pointer;
    &.arrow-up {
      top: @gap;
      animation: arrow-up 2s infinite;
    }
    &.arrow-down {
      top: auto;
      bottom: @gap;
      animation: arrow-down 2s infinite;
    }

  }
  @jump: 20px;
  @keyframes arrow-up {
    0%{
      transform: translate(-50%, @jump);
    }
    50% {
      transform: translate(-50%, -@jump);
    }
    100% {
      transform: translate(-50%, @jump);
    }
  }
   @keyframes arrow-down {
    0%{
      transform: translate(-50%, -@jump);
    }
    50% {
      transform: translate(-50%, @jump);
    }
    100% {
      transform: translate(-50%, -@jump);
    }
  }

  .interceptor {
    .self-center();
    left: auto;
    right: 30px;
    transform: translate(-50%);
    cursor: pointer;
    caret-color: transparent;
  li {
    width: 7px;
    height: 7px;
    background: @words;
    border-radius: 50%;
    border: 1px solid #fff;
    margin-bottom: 10px;
    box-sizing: border-box;
    &.active {
      background: #fff;
    }
  }
  }
</style>
