<template>
    <button type="button" v-show="show" class="to-top-container" @click="handleClick">
        Top
    </button>
</template>
<script>
export default {
    data() {
        return {
            show: false
        }
    },
    created() {
        this.$bus.$on('mainScroll',this.handle);
    },
    destroyed() {
        this.$bus.$off('mainScroll',this.handle)
    },
    methods: {
        handle(dom) {
            if(!dom) {
                this.show = false;
                return;
            }
            this.show = dom.scrollTop >= 500;
            
        },
        handleClick() {
            this.$bus.$emit('setMainScroll',0);
        }
    }
}
</script>

<style scoped lang="less">
@import '~@/styles/var.less';
    .to-top-container {
        width: 50px;
        height: 50px;
        cursor: pointer;
        line-height: 50px;
        text-align: center;
        border-radius: 50%;
        position: fixed;
        right: 50px;
        bottom: 50px;
        z-index: 999;
        background: @primary;
        color: #fff;
        border: 0;
    }
</style>