<style scoped lang="less">
@import '~@/styles/global.less';
@import '~@/styles/var.less';
.contact {
    list-style: none;
    color: @gray;
    margin: 0;
    padding: 20px;
    @itemHeight: 30px;
    li {
      height: @itemHeight;
      line-height: 30px;
      margin: 14px 0;
      position: relative;
      
      &:hover {
        .pop {
          transform: scaleY(1);
        }
      }

    }
    a {
      display: flex;
