      width: 36px;
      &.weixin {
        font-size: 32px;
        text-indent: -3px;
      }
    }
    .pop {
      position: absolute;
      left: 0;
      bottom: @itemHeight + 5px;
      padding: 10px 15px;
      background: #fff;
      border-radius: 5px;
      transform: scaleY(0);
      transform-origin: bottom center;
      transition: 0.3s;
      img {
        width: 150px;
        height: 150px;
      }
      &::after {
        content: "";
        width: 8px;
