<template>
  <div class="blog-comment-container">
    <MessageArea
      :list="data.rows"
      :title="'评论列表'"
      :subTitle="`(${data.total})`"
      :isListLoading="isLoading"
      @submit="handleSubmit"
    />
  </div>
</template>

<script>
import MessageArea from "@/components/MessageArea";
import fetchData from "@/mixins/fetchData";
import { getComments, postComment } from "@/api/blog.js";
export default {
  mixins: [fetchData({ total: 0, rows: [] })],
  components: {
    MessageArea,
  },
  data() {
    return {
      page: 1,
      limit: 10,
    };
  },
  computed: {
    hasMore() {
      return this.data.rows.length < this.data.total;
    }
  },
  mounted() {
    this.$bus.$on('mainScroll',this.handle);

  },
  destroyed() {
    this.$bus.$off('mainScroll',this.handle);
  },
  methods: {
    async fetchData() {
      return await getComments(this.$route.params.id, this.page, this.limit);
    },
    handle(dom) {
      if(this.isLoading || !dom) {
        return;
      }
      const range = 100;
      const des = Math.abs(dom.scrollTop + dom.clientHeight - dom.scrollHeight);
      if(des <= range) {
        this.fetchMore();
      }
    },
    async fetchMore() {
      if(!this.hasMore) {
        return;
      }
      this.isLoading = true;
      const req = await this.fetchData();
      this.page++;
      this.data.rows = this.data.rows.concat(req.rows);
      this.data.total = resp.total;
      this.isLoading = false;
    },
    async handleSubmit(fromData, callback) {
      const req = await postComment({
        blogId: this.$route.params.id,
        ...fromData
      });
      this.data.rows.unshift(req);
      this.data.total++;
      callback("评论成功！");
    }
  },
};
</script>

<style lang="less" scoped>
.blog-comment-container {
  padding: 50px;
}
</style>