<template>
    <div class="about-container" v-loading="loading || !srcLoading">
        <iframe class="about-content"
        v-if="src" 
        :src="src"
        @load="srcLoading = true"
        frameborder="0"
         ></iframe>
    </div>
</template> 

<script>
import { mapState } from "vuex";
export default {
    computed: mapState('about', {
        src: "data",
        loading: "loading"
    }),
    created() {
        this.$store.dispatch("about/fetchAbout");
    },
    data() {
        return {
            srcLoading: false
        }
    }
}


</script>

<style scoped lang="less">
.about-container {
  width: 100%;
  height: 100%; 
  overflow: hidden;
  position: relative;
}
.about-content {
  width: 100%;
  height: 100%;
  display: block;
  box-sizing: border-box;
}

</style>
