import { getSetting } from "@/api/setting.js";
import { titleController } from "@/utils";
export default {
    namespaced: true,
    state: {
        loading: false,
        data: null
    },
    mutations: {
        setLoading(state, payload) {
            state.loading = payload;
        },
        setData(state, payload) {
            state.data = payload;
        }
    },
    actions: {
        async fetchSetting(ctx) {
            ctx.commit('setLoading', true);
            const resq = await getSetting();

            ctx.commit('setData', resq);
            ctx.commit('setLoading',false);
            if(resq.favicon) {
                let link = document.querySelector("link[ref='shortcut icon']");
                if(link) {
                    return;
                }
                link = document.createElement('link');
                link.rel = "shortcut icon";
                link.type = "images/x-icon";
                link.href = resq.favicon;
                document.querySelector('head').appendChild(link);
            }
            if(resq.siteTitle) {
                titleController.setSideTitle(resq.siteTitle);
            }
        }
        
    }
}